@import url(https://fonts.googleapis.com/css2?family=Sriracha&display=swap);
* {
  box-sizing: border-box;
}

body {
  margin: 0;
}

.slide-in-left-enter {
  transform: translateX(-100%);
}

.slide-in-left-enter-active {
  transform: translateX(0);
  opacity: 1;
  transition: all 200ms;
}

.slide-in-left-exit {
  transform: translateX(0%);
  opacity: 1;
}

.slide-in-left-exit-active {
  transform: translateX(-100%);
  opacity: 0;
  transition: all 200ms;
}

.center {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.users-catalog {
  list-style: none;
  margin: 7rem auto;
  padding: 0;
  width: 90%;
  max-width: 50rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.user-bit {
  /* width: calc(45% - 2rem);
  min-width: 17.5rem; */
  width: 20rem;
}

.user-bit__li {
  margin: 1rem 1rem;
}

.user-bit a {
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
  text-decoration: none;
  padding: 1rem;
  color: #292929;
  background: #ffffff;
}

.user-bit #noClick {
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
  text-decoration: none;
  padding: 1rem;
  color: #292929;
  background: #ffffff;
}

.user-bit #noClick:hover {
  background: #292929;
}

.user-bit a:hover,
.user-bit a:active {
  background: #292929;
}

.user-bit__content {
  padding: 0 !important;
}

.user-bit__image {
  width: 4rem;
  height: 4rem;
  margin-right: 1rem;
}

.user-bit__info h4 {
  font-size: 1.5rem;
  margin: 0 0 0.5rem 0;
  font-weight: normal;
  color: #292929;
}

.user-bit:hover h4,
.user-bit:active h4,
.user-bit:hover h6,
.user-bit:active h6 {
  color: #0082be;
}

.user-bit__info h6 {
  margin: 0;
}

.avatar {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.avatar img {
  display: block;
  border-radius: 50%;
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-style: solid;
  border-color: #0082be;
}

.card-catalog {
  margin: 0 auto;
  border: 1px solid;
  border-color: lightgray;
  border-radius: 3px;
  padding: 1rem;
  overflow: hidden;
  background: white;
}

.post-form {
  margin: 7rem auto;
  max-width: 40rem;
  width: 80%;
}

.post-desc {
  min-height: 7rem !important;
}

.main-nav__menu-btn {
  width: 3rem;
  height: 3rem;
  background: transparent;
  border: none;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  margin-right: 2rem;
  cursor: pointer;
}

.main-nav__menu-btn span {
  display: block;
  width: 3rem;
  height: 2.5px;
  background: white;
}

.main-nav__title {
  color: white;
}

.main-nav__title a {
  text-decoration: none;
  color: white;
}

.main-nav__wrap-nav {
  display: none;
}

.main-nav__side-bar {
  height: 100%;
}

.logo1 {
  font-size: 1.9rem;
  font-family: "Sriracha", cursive;
}

.logo1 span:hover {
  color: #0082be;
}

@media (min-width: 768px) {
  .main-nav__menu-btn {
    display: none;
  }

  .main-nav__wrap-nav {
    display: block;
  }
}

.main-wrap {
  width: 100%;
  height: 3.5rem;
  display: flex;
  align-items: center;
  justify-content: space-around;
  position: fixed;
  top: 0;
  left: 0;
  background: #292929;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.26);
  padding: 0 1rem;
  z-index: 5;
}

main {
  margin-top: 5rem;
}

@media (min-width: 768px) {
  .main-header {
    justify-content: space-between;
  }
}

.nav-links {
  list-style: none;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.nav-links a:hover {
  color: #0082be;
}

.nav-links a {
  border: 1px solid transparent;
  color: #292929;
  text-decoration: none;
  padding: 0.5rem;
}

.nav-links button {
  cursor: pointer;
  border: 1px solid #292929;
  color: #292929;
  background: transparent;
  padding: 0.5rem;
  font: inherit;
}

.nav-links button:focus {
  outline: none;
}

.nav-links button:hover,
.nav-links button:active {
  background: #292929;
  color: white;
}
@media (min-width: 768px) {
  .nav-links {
    flex-direction: row;
  }

  .nav-links li {
    margin: 0 0.5rem;
  }

  .nav-links a {
    color: white;
    text-decoration: none;
  }

  .nav-links button {
    border: 1px solid white;
    color: white;
    background: transparent;
  }

  .nav-links button:hover,
  .nav-links button:active {
    background: #f8df00;
    color: #292929;
  }
}

.side-bar {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 100;
  height: 100vh;
  width: 70%;
  background: #2a2a2a;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
}

.side-bar a {
  color: #fff;
  font-size: 1.2rem;
}

.side-bar li {
  margin-top: 2rem;
}

.shadow {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.5);
  z-index: 10;
}

.post-catalog {
  list-style: none;
  margin: 7rem auto;
  padding: 0;
  width: 90%;
  max-width: 40rem;
}

.post-bit {
  margin: 2rem 0;
  padding-top: 2rem;
  width: 600px;
}

.post-bit__content {
  padding: 0;
}

.post-bit__info {
  padding: 1rem;
}

.post-bit__desc {
  font-size: 0.8rem;
}

.post-bit__image {
  width: 100%;
  height: 40rem !important;
  margin-right: 1.5rem;
}

.post-bit__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* .post-head {
  height: 3.3rem;
  display: flex;
} */

.user__image {
  width: 2.3rem;
  height: 2.3rem;
  margin: 0.8rem 0 0 1rem;
  position: absolute;
}

/* .creatorName {
  margin: 0.75rem 0 0 1rem;
} */

.post-bit__info h2,
.post-bit__info h3,
.post-bit__info p {
  margin: 0 0 0.5rem 0;
}

.post-bit__actions {
  padding: 1rem;
  text-align: center;
  border-top: 1px solid #ccc;
}

.post-bit__modal-content {
  padding: 0;
}

.post-bit__modal-actions {
  text-align: right;
}

.post-bit__actions button,
.post-bit__actions a {
  margin: 0.5rem;
}

.map-wrap {
  height: 30rem;
  width: 100%;
}

.post-btn {
  width: 10rem;
  background-color: #bdbdbd !important;
}

.post-btn:hover {
  background-color: #005780 !important;
}

.delete-footer {
  margin: 1rem 1.8rem 1rem 0;
}

@media (min-width: 1201px) {
  .map-wrap {
    height: 45rem;
  }
}

@media (min-width: 768px) {
  .post-bit__image {
    height: 20rem;
  }
}

@media (max-width: 768px) {
  .map-wrap {
    height: 26rem;
  }

  .post-bit {
    width: 21rem;
    margin: 0 !important;
  }

  .post-btn {
    width: 5.5rem;
    height: 2rem;
    padding: 0rem 0.5rem;
    font-size: 8pt;
    margin: 0.2rem !important;
    background-color: #2a2a2a !important;
  }
}

.modal1 {
  z-index: 100;
  position: fixed;
  top: 10%;
  left: 12%;
  width: 75%;
  background: white;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
}

.modal__header {
  display: flex;
  justify-content: space-between;
  width: 100%;
  padding: 0.5rem 1rem;
  background: #2a2a2a;
  font-size: 0.7rem !important;
  color: white;
}

.modal-prompt {
  text-align: center;
}

.modal__header h2 {
  margin: 0.5rem;
}

.x-btn {
  background-color: transparent;
  border: none;
  color: white;
}

@media (min-width: 1201px) {
  .modal-prompt {
    width: 25rem;
    left: 38% !important;
  }
}

@media (max-width: 1024px) {
  .modal-prompt {
    width: 25rem;
    left: 28%;
  }
}

@media (min-width: 768px) {
  .modal {
    left: calc(50% - 20rem);
    width: 40rem;
  }

  .modal-prompt {
    width: 25rem;
    left: 28%;
  }
}

.modal-enter {
  transform: translateY(-10rem);
  opacity: 0;
}

.modal-enter-active {
  transform: translateY(0);
  opacity: 1;
  transition: all 200ms;
}

.modal-exit {
  transform: translateY(0);
  opacity: 1;
}

.modal-exit-active {
  transform: translateY(-10rem);
  opacity: 0;
  transition: all 200ms;
}

@media (max-width: 768px) {
  .modal1 {
    width: 94%;
    left: 3%;
  }
}

.map {
  width: 100%;
  height: 100%;
}

#menu {
  position: absolute;
  background: #2a2a2a;
  padding: 10px;
  font-family: "Open Sans", sans-serif;
  width: 100%;
  color: white;
}

.spacer {
  margin-right: 1.3rem;
}

@media (max-width: 768px) {
  .spacer {
    margin-right: 0.6rem;
  }

  #menu {
    font-size: 0.8rem;
  }
}

.log-form {
  margin: 7rem auto;
  max-width: 25rem;
  width: 80%;
}

.log2 {
  text-align: center;
  margin-top: -6rem !important;
}

.log2 p {
  margin-bottom: -0.09rem;
}

.agree {
  font-size: 14px;
}

.error-notice {
  margin-bottom: 3rem;
  padding: 0.5rem;
  border: 1px solid #e07c7c;
  border-radius: 8px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #f8d6d6;
}
.error-notice button {
  background: transparent;
  border: none;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  margin-top: -0.2rem;
}

